import React from 'react';
import Dots from 'components/Dots';
import addComma from 'utils/addComma';
import {
  Container,
  ImageBlock,
  ContentBlock,
  RecommandBlock,
  Image,
  Title,
  Description,
  Price,
  Recommand,
  ContentBottomBlock,
} from './styled';

const TicketCard = ({ link, title, image, price, recommand, description }) => {
  return (
    <Container href={link}>
      <ImageBlock>
        <Image src={image} />
      </ImageBlock>

      <ContentBlock>
        <Title>{title}</Title>

        <Description>{description}</Description>

        <ContentBottomBlock>
          <RecommandBlock>
            <Recommand>推薦度</Recommand>
            <Dots count={recommand} />
          </RecommandBlock>

          <Price>${addComma(price)}/人</Price>
        </ContentBottomBlock>
      </ContentBlock>
    </Container>
  );
};

export default TicketCard;
