import React from 'react';
import { Container, Image, Tag, Title } from './styled';

const ParagraphCard = ({ image, tag, title, link, ...props }) => {
  return (
    <Container href={link} {...props}>
      <Image src={image} />
      <Tag>{tag}</Tag>
      <Title>{title}</Title>
    </Container>
  );
};

export default ParagraphCard;
