import React from 'react';
import { Container, DotFill, DotOutline } from './styled';

const MAX_COUNT = 5;

const Dots = ({ count = 0 }) => {
  return (
    <Container>
      {count &&
        Array(count)
          .fill(0)
          .map((_, index) => <DotFill key={`dot-fill-${index}`} />)}

      {Array(MAX_COUNT - count)
        .fill(0)
        .map((_, index) => (
          <DotOutline key={`dot-fill-${index}`} />
        ))}
    </Container>
  );
};

export default Dots;
