import React from 'react';
import Checkbox from 'components/Checkbox';
import ParagraphCard from 'components/ParagraphCard';
import MoreButton from 'components/MoreButton';
import {
  Container,
  Wrapper,
  TinyText,
  Title,
  Description,
  TopBlock,
  BottomBlock,
  VideoBlock,
  FakeVideo,
  InfoBlock,
} from 'components/LazypackAttraction/styled';
import { FormBlock, CheckboxBlock, CheckboxTitle, FieldBlock } from './styled';

const PARAGRAPH_LIST = [
  {
    id: 'lazypack-1',
    tag: '旅遊 Travel',
    title:
      '文章標題文章標題文章標題文章標題文章標題文章標題文章章標題文章章標題文章',
    image: 'https://fakeimg.pl/300x200',
    link: '',
  },
  {
    id: 'lazypack-2',
    tag: '旅遊 Travel',
    title:
      '文章標題文章標題文章標題文章標題文章標題文章標題文章章標題文章章標題文章',
    image: 'https://fakeimg.pl/300x200',
    link: '',
  },
  {
    id: 'lazypack-3',
    tag: '旅遊 Travel',
    title:
      '文章標題文章標題文章標題文章標題文章標題文章標題文章章標題文章章標題文章',
    image: 'https://fakeimg.pl/300x200',
    link: '',
  },
];

const LazypackSection = () => {
  // NOTE: 選取的所有標籤
  const [selectedTags, setSelectedTags] = React.useState([]);

  const handleCheckboxChange = event => {
    if (event.target.checked) {
      const newSelectedTags = [...selectedTags];
      newSelectedTags.push(event.target.value);
      setSelectedTags(newSelectedTags);
    } else {
      const newSelectedTags = [...selectedTags];
      const targetIndex = newSelectedTags.indexOf(event.target.value);
      newSelectedTags.splice(targetIndex, 1);
      setSelectedTags(newSelectedTags);
    }
  };

  return (
    <Container style={{ backgroundColor: '#ffffff' }}>
      <Wrapper>
        <TopBlock>
          <InfoBlock>
            <TinyText>A reas</TinyText>
            <Title>
              懶人包
              <br />
              精選行程
            </Title>
            <Description>選個選項來看看行程吧!</Description>
          </InfoBlock>

          <FormBlock>
            <FieldBlock style={{ marginBottom: 24 }}>
              <CheckboxTitle>天數</CheckboxTitle>

              <CheckboxBlock>
                <Checkbox
                  label="一日遊"
                  value="0"
                  checked={selectedTags.includes('0')}
                  onChange={handleCheckboxChange}
                />
                <Checkbox
                  label="兩天一夜"
                  value="1"
                  checked={selectedTags.includes('1')}
                  onChange={handleCheckboxChange}
                />
                <Checkbox
                  label="三天兩夜"
                  value="2"
                  checked={selectedTags.includes('2')}
                  onChange={handleCheckboxChange}
                />
              </CheckboxBlock>
            </FieldBlock>

            <FieldBlock>
              <CheckboxTitle>風格</CheckboxTitle>

              <CheckboxBlock>
                <Checkbox
                  label="都會感"
                  value="3"
                  checked={selectedTags.includes('3')}
                  onChange={handleCheckboxChange}
                />
                <Checkbox
                  label="戶外風"
                  value="4"
                  checked={selectedTags.includes('4')}
                  onChange={handleCheckboxChange}
                />
              </CheckboxBlock>
            </FieldBlock>
          </FormBlock>
        </TopBlock>

        <BottomBlock>
          <FakeVideo src="https://fakeimg.pl/300x200" />

          <VideoBlock></VideoBlock>

          <MoreButton>看更多影片</MoreButton>

          {PARAGRAPH_LIST.map(paragraph => (
            <ParagraphCard
              key={paragraph.id}
              tag={paragraph.tag}
              title={paragraph.title}
              image={paragraph.image}
            />
          ))}
          <MoreButton style={{ marginBottom: 0 }}>看更多文章</MoreButton>
        </BottomBlock>
      </Wrapper>
    </Container>
  );
};

export default LazypackSection;
