import React from 'react';
import { Container, Wrapper, CheckboxInput, Text } from './styled';

const Checkbox = ({ label, value, checked, onChange = () => {} }) => {
  return (
    <Container>
      <Wrapper>
        <CheckboxInput checked={checked} value={value} onChange={onChange} />
        <Text>{label}</Text>
      </Wrapper>
    </Container>
  );
};
export default Checkbox;
