import React from 'react';
import { Autoplay, EffectCreative } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import useIsMobile from 'hooks/useIsMobile';
import carouselImage1 from 'images/carousel-image-1.png';
import carouselImage2 from 'images/carousel-image-2.png';
import carouselImage3 from 'images/carousel-image-3.png';
import carouselImage4 from 'images/carousel-image-4.png';
import carouselImage5 from 'images/carousel-image-5.png';
import carouselImage6 from 'images/carousel-image-6.png';
import mobileCarouselImage1 from 'images/mobile-carousel-image-1.png';
import mobileCarouselImage2 from 'images/mobile-carousel-image-2.png';
import mobileCarouselImage3 from 'images/mobile-carousel-image-3.png';
import mobileCarouselImage4 from 'images/mobile-carousel-image-4.png';
import mobileCarouselImage5 from 'images/mobile-carousel-image-5.png';
import mobileCarouselImage6 from 'images/mobile-carousel-image-6.png';
import {
  Container,
  TopBlock,
  BottomBlock,
  TextImage,
  CarouselImage,
} from './styled';

const IMAGE_LIST = [
  carouselImage1,
  carouselImage2,
  carouselImage3,
  carouselImage4,
  carouselImage5,
  carouselImage6,
];

const MOBILE_IMAGE_LIST = [
  mobileCarouselImage1,
  mobileCarouselImage2,
  mobileCarouselImage3,
  mobileCarouselImage4,
  mobileCarouselImage5,
  mobileCarouselImage6,
];

const TopSection = () => {
  const isMobile = useIsMobile();
  // const windowHeight = window.innerHeight

  const imageList = isMobile ? MOBILE_IMAGE_LIST : IMAGE_LIST;

  return (
    <Container>
      {/* 圖片輪播 */}
      <Swiper
        loop
        noSwiping
        centeredSlides
        spaceBetween={0}
        noSwipingClass="swiper-slide"
        autoplay={{ delay: 3000 }}
        slidesPerView={isMobile ? 1.5 : 2}
        modules={[Autoplay, EffectCreative]}
        effect="creative"
        creativeEffect={{
          limitProgress: 2,
          prev: {
            scale: 0.8,
            translate: ['-95%', 0, 0],
          },
          next: {
            scale: 0.8,
            translate: ['95%', 0, 0],
          },
        }}
      >
        {imageList.map((image, index) => (
          <SwiperSlide key={`slide-image-${index}`}>
            <CarouselImage src={image} />
          </SwiperSlide>
        ))}
      </Swiper>

      {/* 按鈕區塊 */}
      <BottomBlock>
        <TextImage />
      </BottomBlock>
    </Container>
  );
};

export default TopSection;
